/* ==========================================================================
   Base styles: opinionated defaults
   ========================================================================== */

   html {
    background-color: #000000;
}

/* Style for the waveform canvas */
#waveform {
    width: 100%;
    height: 150px;
    background-color: transparent;
    position: absolute;
    bottom: 0;
    left: 0;
}

/* Style for section titles */
.section-titles {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    margin-top: -50px; /* Adjusted margin to move it up */
}

.title-inline .minititle {
  font-size: 28px;
}

.title-inline .title-button {
  font-size: 65px;
}


.text-effect {
    font-family: 'Audiowide', sans-serif;
    font-size: 36px;
    background-image: linear-gradient(to right, #aeccff, #507ccc, #1867b0);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
}

.moveBox {
    margin: 0 auto;
    width: 300px;
}

/* ==========================================================================
  Helper classes
  ========================================================================== */

/*
* Hide visually and from screen readers
*/

.hidden,
[hidden] {
    display: none !important;
}

/*
* Hide only visually, but have it available for screen readers:
* https://snook.ca/archives/html_and_css/hiding-content-for-accessibility
*
* 1. For long content, line feeds are not interpreted as spaces and small width
*    causes content to wrap 1 word per line:
*    https://medium.com/@jessebeach/beware-smushed-off-screen-accessible-text-5952a4c2cbfe
*/

.sr-only {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    white-space: nowrap;
    width: 1px;
    /* 1 */
}

/*
* Extends the .sr-only class to allow the element
* to be focusable when navigated to via the keyboard:
* https://www.drupal.org/node/897638
*/

.sr-only.focusable:active,
.sr-only.focusable:focus {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    white-space: inherit;
    width: auto;
}

.gallery {
    position: relative;
    margin-top: 20px;
    margin-bottom: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.photo {
    display: none;
}

.photo.active {
    display: block;
}

.photo img {
    width: 640px;
    height: 360px;
}




/* Style for the "prev" and "next" buttons */
.prev, .next {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 16px;
}

.prev {
    left: 10px;
}

.next {
    right: 10px;
}

/*
* Hide visually and from screen readers, but maintain layout
*/

.invisible {
    visibility: hidden;
}

/*
* Clearfix: contain floats
*
* For modern browsers
* 1. The space content is one way to avoid an Opera bug when the
*    `contenteditable` attribute is included anywhere else in the document.
*    Otherwise it causes space to appear at the top and bottom of elements
*    that receive the `clearfix` class.
* 2. The use of `table` rather than `block` is only necessary if using
*    `:before` to contain the top-margins of child elements.
*/

.clearfix::before,
.clearfix::after {
    content: " ";
    display: table;
}

.clearfix::after {
    clear: both;
}

